@import url(https://fonts.googleapis.com/css?family=Lora:400,700);
@import url(https://fonts.googleapis.com/css?family=Princess+Sofia|Roboto+Condensed);
@import url(https://cdnjs.cloudflare.com/ajax/libs/bulma/0.6.1/css/bulma.min.css);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.card {
  font-family: Roboto Condensed,sans-serif;
  font-weight: 700;
  float: left;
  display: -webkit-flex;
  display: flex;
  margin: 0 10px 10px 0;
  width: 150px;
  height: 200px;
  background: -webkit-linear-gradient(right bottom, white, #f0f0f0);
  background: linear-gradient(to left top, white, #f0f0f0);
  border-radius: 5px;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2), inset 0 0 0 1px rgba(0, 0, 0, 0.2);
  position: relative;
  color: #152624;
  cursor: pointer;
}

.card.red {
  color: #ED143D;
}
.card .corner {
  position: absolute;
  text-align: center;
}
.card .corner.top {
  top: 10px;
}
.card .corner.left {
  left: 10px;
}
.card .corner.bottom {
  bottom: 10px;
}
.card .corner.right {
  right: 10px;
}
.card .corner h1 {
  font-size: 1.5em;
  margin: 0;
}
.card .corner .pip {
  font-size: 1.3em;
}
.card .corner:last-of-type {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.card .pip {
  font-family: serif;
}
.card .pip.clubs:before {
  content: '\2663';
}
.card .pip.hearts:before {
  content: '\2665';
}
.card .pip.spades:before {
  content: '\2660';
}
.card .pip.diamonds:before {
  content: '\2666';
}
.card .pips.center-pip {
  width: 100%;
  height: 100%;
  margin: auto;
  border-radius: 4px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 60%;
}
.card-rank {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 20px;
}
.card-label {
  position: absolute;
  bottom: 0;
  font-size: 12px;
  width: 100%;
  padding: 5px;
  font-family: Roboto Condensed,sans-serif;
}
.card .pips .pip {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 2.3em;
}
.card .pips .pip.flip {
  -webkit-transform: translate(-50%, -50%) rotate(180deg);
          transform: translate(-50%, -50%) rotate(180deg);
}
.card .pips h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 4em;
  border-width: 1px;
  border-style: solid;
  text-align: center;
  line-height: 125px;
  width: 50%;
  margin: 0;
}

.Player-shield {
  position: relative;
}

.Player-shieldRank {
  position: absolute;
  z-index: 1;
  top: 25%;
}

html,
body {
  background-color: #152624;
  height: 100%;
  font-family: 'Roboto Condensed', sans-serif;
  color: #777;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #152624;
  padding: 20px;
}

.App-title {
  font-size: 2em;
  color: #fff;
  text-shadow: 0 1px 0 rgba(255, 255, 255, .09);
}

.App-intro {
  font-size: large;
  margin-bottom: 10px;
}

.App-room {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  padding: 25px 0;
  overflow: hidden;
}
@media (max-width: 576px) {
  .App-room > .card:not(:first-child) { margin: 0 0 0 -75px; }
}

.App-shield {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: -75px;
}
@media (max-width: 576px) {
  .App-shield { margin-top: -150px; }
}
.App-shield .card {
  box-shadow: 0 0 0 10px rgba(67, 170, 139, .1), 0 10px 30px rgba(67, 170, 139, 0.5), inset 0 0 0 1px rgba(0, 0, 0, 0.2);
  width: 180px;
  height: 230px;
}

@-webkit-keyframes animate-width {
  0% {
    width: 0;
  }
  100% {
    visibility: visible;
  }
}

@keyframes animate-width {
  0% {
    width: 0;
  }
  100% {
    visibility: visible;
  }
}
.progress-bar {
  margin: 0 0 10px;
}

.progress-bar-rating {
  position: relative;
  border-radius: 4px;
  display: inline-block;
  width: 0;
  height: 10px;
  font-size: 10px;
  text-indent: -999em;
  overflow: hidden;
  line-height: 10px;
  color: white;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
  background-image: -webkit-linear-gradient(top, #F15471 0%, #F15471 47%, #ED143D 50%, #ED143D 100%);
  background-image: linear-gradient(to bottom, #F15471 0%, #F15471 47%, #ED143D 50%, #ED143D 100%);
  width: 0;
  -webkit-transition: all 0.25s cubic-bezier(0.26, 0.01, 0.51, 1);
  transition: all 0.25s cubic-bezier(0.26, 0.01, 0.51, 1);
}

.button{
  background:transparent;
  -webkit-transition:all .5s ease;
  transition:all .5s ease;
  color:#fff;
  outline:none;
  box-shadow: 20px 38px 34px -26px hsla(0,0%,0%,.2);
  border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
}
.button:hover{
  color: #ddd;
  box-shadow:2px 8px 4px -6px hsla(0,0%,0%,.3);
}

.button.lined.thin{
  border:solid 2px #777;
}

.button[disabled] {
  background-color: transparent;
}
.button[disabled]:hover {
  color: #777;
  background-color: transparent;
}

.text-white {color: #fff;}

.text-black {color: #000;}

.img-responsive {
  display: block;
  margin: auto;
  max-width: 100%;
}

.mb3 {margin-bottom: 60px}

.knot {width: 150px;}

